@import url();
*{
  box-sizing: border-box;
}
html{
  font-family: 'Noto sans Jp', sans-serif;
}
body{
  margin: 0;
  background-color: #b7ffd5;
}

  
  
.expenses{
    padding: 1rem;
    background-color: rgb(54, 56, 65);
    margin: 2rem auto;
    width: 50rem;
    max-width: 95%;
   
};
  
  
   

.expense-item {
    display: flex;
    justify-content: space-between;
    align-items: center;
    box-shadow: 0 1px 8px rgba(0, 0, 0, 0.25);
    padding: 0.5rem;
    margin: 1rem 0;
    border-radius: 12px;
    background-color: #34495e;
}
.expense-item__description {
    display: flex;
    flex-direction: column;
    grid-gap: 1rem;
    gap: 1rem;
    align-items: flex-end;
    flex-flow: column-reverse;
    justify-content:flex-start;
    flex: 1 1;

}
.expense-item h2{
    color: #3a3a3a;
    font-size: 1rem;
    flex: 1 1;
    margin: 0 1rem;
    color: white;
}
.expense-item__price {
    font-size: 1rem;
    font-weight: bold;
    color: white;
    background-color: #30336b;
    border: 1px solid #ffffff;
    padding: 0.5rem;
    border-radius: 12px;
}
@media (min-width:500px){
    .expense-item__description{
        flex-direction: row;
        align-items: center;
        justify-content: flex-start;
        flex: 1 1;
    }
    .expense-item__description h2{
        font-size: 1.25rem;
    }
    .expense-item__price {
        font-size: 1.25rem;
        padding: 0.5rem 1.5rem;
    }
}

.expense-date {
    display: flex;
    flex-direction: column;
    width: 5.5rem;
    height: 5.5rem;
    border: 1px solid #ececec;
    background-color: #2a2a2a;
    color: white;
    border-radius: 12px;
    align-items: center;
    justify-content: center;
}

.expense-date__month {
    font-size: 0.75rem;
    font-weight: bold;
    }
.expense-date__year {
    font-size: 00.75rem;
    }
 .expense-date__day {
     font-size: 1.5rem;
     font-weight: bold;
 }       
.card {
  
    border-radius: 12px;
    box-shadow: 0 1px 8px rgba(180, 34, 34, 0.25);
}
.Project-header h1 {
    text-align: center;
    justify-content: center;
    font-weight: bold;
    font-size: 3rem;
    text-transform: capitalize;
    text-shadow: 0px 2px 6px red;
   
}
.Project-header marquee {
    font-weight: bold;
     text-transform: capitalize;
    text-shadow: 0px 2px 6px violet;
    
}

.new-expense{
    background-color: #a892ee;
    padding: 1rem;
    margin: 2rem auto;
    width: 50rem;
    max-width: 95%;
    border-radius: 12px;
    text-align: center;
    box-shadow: 0 1px 8px rgba(0, 0, 0, 0.25);
}
.new-expense button {
    font: inherit;
    cursor: pointer;
    padding: 1rem 2rem;
    border: 1px solid #40005d;
    background-color: #40005d;
    color: white;
    border-radius: 12px;
    margin-left:  5rem;
    
    
}
.new-expense button:hover,
.new-expense button:active {
    background-color: #510676;
    border-color: #510676;
}
.new-expense button.alternative {
    color: #220133;
    border-color: transparent;
    background-color: transparent;
}
.new-expense button.alternative:hover,
.new-expense button.alternative:active {
    background-color: #ddb3f8;
}
.new-expense__controls {
    display: flex;
    flex-wrap: wrap;
    grid-gap: 1rem;
    gap: 1rem;
    text-align: left;

}
.new-expense__controls label {
    font-weight: bold;
    margin-bottom: 0.5rem;
    display: block;
    }
    .new-expense__controls input {
        font: inherit;
        padding: 0.5rem;
        border-radius: 6px;
        border: 1px solid #ccc;
        width: 20rem;
        max-width: 100%;

    }
    .new-expense__actions {
        text-align: left;
        
        
    }
